import * as yup from 'yup'
import { urlOption, telegramOption } from '../schemas'
import { FILE_SIZE, PHONE_REG_EXP } from '../constants'

class ProjectCreationSchema {
  static readonly tabPC2 = yup.object().shape({
    project_name: yup.string().required('Укажи название проекта'),
    description: yup
      .string()
      .required('Расскажи о проекте')
      .min(80, `Возможная длина поля 80-5000 символов`)
      .max(5000, `Возможная длина поля 80-5000 символов`),
    tech_doc_link: urlOption.required('Укажите ссылку на документацию').when('isDocNotRequired', {
      is: true,
      then: (theme) => theme.notRequired(),
    }),
  })

  static readonly tabPC3 = yup.object().shape({
    sphere: yup.string().required('Укажи сферу'),
    direction: yup.string().required('Укажи направление'),
    pet_project_count: yup
      .number()
      .required('Укажи длительность проекта')
      .min(1, 'Длительность не может быть меньше 1 месяца'),
  })

  static readonly tabPC4 = yup.object().shape({
    // TODO не заработало и закостылил
    // company_name: yup.lazy((value) => {
    //   switch (typeof value) {
    //     case 'object':
    //       return yup.object().required('Выбери заказчика').nullable(true) // schema for object
    //     case 'string':
    //       return yup.string().required('Выбери заказчика').nullable(true) // schema for string
    //     default:
    //       return yup.object().required('Выбери заказчика').nullable(true)
    //   }
    // }),
    telegram: telegramOption,
    email: yup.string().email('Введи корректный адрес электронной почты').nullable(true).trim(),
    whatsapp: yup
      .string()
      .nullable()
      .transform((curr, orig) => (orig === '' ? null : curr))
      .matches(PHONE_REG_EXP, 'Введи корректный номер телефона')
      .trim(),
  })

  static readonly tabPC23 = yup.object().shape({
    requirements: yup
      .string()
      .required('Укажи требования')
      .min(3, `Возможная длина поля 3-500 символов`)
      .max(500, `Возможная длина поля 3-500 символов`),
  })

  static readonly tabPC31 = yup.object().shape({
    customer_comment: yup
      .string()
      .required('Укажи требования')
      .min(3, `Возможная длина поля 3-500 символов`)
      .max(500, `Возможная длина поля 3-500 символов`),
  })

  static readonly tabPC32 = yup.object().shape({
    big_cover: yup
      .mixed()
      .test(
        'fileSize',
        `Максимальный размер файла ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
    small_cover: yup
      .mixed()
      .test(
        'fileSize',
        `Максимальный размер файла ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
  })
}

export default ProjectCreationSchema
